import randomInteger from "random-int";
import React from "react";
import "./farewell.scss";
import styled, { keyframes } from "styled-components";

const snowflakes = [];
for (let i = 0; i < 200; i++) {
	let random_x = randomInteger(0, 100);
	let offset = randomInteger(-10, 10);
	let yoyo_time = randomInteger(30, 80);
	let random_scale = Math.random();

	const flakeAnimation = keyframes`
		${yoyo_time}% { transform: translate(${random_x + offset}vw, ${yoyo_time}vh) scale(${random_scale});}
    to { transform: translate(${random_x + offset / 2}vw, 100vh) scale(${random_scale});}
	`;

	let Flake = styled.div`
		filter:
			opacity: ${Math.max(0.1, Math.random())};
			transform: translate(${random_x}vw, -10px);
			animation: ${flakeAnimation} ${randomInteger(10, 30)}s ${-randomInteger(30)}s linear infinite;
	`;
	snowflakes.push(<Flake className="snowflake" key={`flake${i}`} />);
}

const test = [
	"In these past few years, I've written so much and deleted so much that I have no other choice but to conclude it's just not possible to say everything. There's simply too much to say and not enough time to say it. Despite all the work I put into this page, I honestly doubt that most would find this link. After all, it takes the right combination of curiosity, skill and maybe luck to even get here, however easy the riddle is.",
	"Sometimes I desperately wish I don't have to die but I trust myself more than anybody. It might be hard to believe but I'm happy, happy that I truly got to experience as much as I have. It's just that I feel like I shouldn't be with people. I'm different but that's perfectly okay and I love people too. It's just that the two don't mix. The reason I live and die will always be because of people.",
	"I'm not too sad that I won't get to meet more people and do more things, I'm glad to have done as much as I already have. The only regret I'd have is not being able to watch over certain people but I trust them and life must always move forward. My only hope is that everyone I've ever met is happy and stays happy. I love you all, I really do.",
	"P.S. - I hope this isn't too badly written because I didn't proofread at all and I wanted to write straight from the mind.",
	"P.P.S. - Seriously, don't be sad. Otherwise I'll come back!!",
]

const Farewell = () => {
	return (
		<div className="farewell">
			<div className="snow">{snowflakes}</div>
			<div className="boring-essay">
				{test.map(paragraph => <p>{paragraph}</p>)}
				<a href="https://www.youtube.com/watch?v=RuIXOBF0eyQ">Good morning,</a>
				<a href="https://www.youtube.com/watch?v=Pi9J4epTWGM">good evening,</a>
				<a href="https://www.youtube.com/watch?v=B3vsRXOSBqc">and goodbye.</a>
			</div>
		</div>
	);
};

// I'll never forget you three: Alice, Frank, Rachael
export default Farewell;
